html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: center;
  min-height: 100%;
  background-image: url('images/bg-countdown.jpg');
  background-size: cover;
  background-repeat: no-repeat;

}

.overlay1 {
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.8);
}

.CountDownApp {
  display: table;
}

.CountDownApp-logo {
  height: 80px;
}

.CountDownApp-header {
  /*background-color: #222;*/
  display: table-cell;
  padding: 20px;
  color: white;
  vertical-align: middle;
}

.CountDownApp-header .title{
  margin-top: 40px;
}

.CountDownApp-header a{
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  transition: .2s all;
}
.CountDownApp-header a:hover{
  opacity: .7;
}

.Countdown{
  margin: 10px auto;
  padding-bottom: 20px;
}

.Countdown-col{
  display: inline-block;
}

.Countdown-col-element{
  display: inline-block;
  margin: 0 20px;
  display: flex;
  flex-direction: column;
}

.Countdown-col-element strong{
  font-size: 50px;
}
